//import scrollTo from 'react-scroll-to-component'

class Nav extends React.Component {

	constructor(props) {
		super(props)
	}

	componentDidMount() {
		this.scrollTo = require('react-scroll-to-component')
	}

	render () {
		return (
			<nav className="main-nav">
					{this.props.links.map((link, i) => {
						return (
							<a className="main-nav__link"
							   key={i}
							   href={link.href}
							   onClick={(event) => {
									this.props.changeSection.bind(null, link.href).bind(this)
							   }}
							>
								{link.name}
							</a>
						)
					})}
			</nav>
		)
	}

}

export default Nav
